<template>
  <div class="trade">
    <div class="commonHead flexcenter cfff pre boxmlr">
      币币兑换
      <img class="caidan" src="../../../static/caidan.png" />
    </div>
    <div class="main boxmlr ovh">
      <div class="coinType boxplr bgfff">
        <div class="title pre ibk">
           <span>选择要兑换的币种</span>
              <span class="underline"></span>
          </div>
        <div class="selectBox flex flexjcsb">
          <div>兑换</div>
          <div class="coinType" >
            <span>ETS</span>
            <span class="iconfont icon-arrow-sl"></span>
          </div>
        </div>
        <div class="selectBox flex flexjcsb">
          <div>接收</div>
          <div class="coinType" @click="showSelectCoin('toCoin')">
            <span>{{toExchange}}</span>
            <span class="iconfont icon-arrow-sl"></span>
          </div>
        </div>
      </div>
      <div class="coinMoney boxplr bgfff">
        <div class="title pre ibk">金额
          <span class="underline"></span>
        </div>
        <div class="selectBox flex flexjcsb">
          <div>
            <span class="type ibk">ETS</span> 
            <input :placeholder="`当前余额 ${$store.state.player.etsFreeAmount} ETS`"  v-model="exchangeNumber" />
          </div>
          <div class="checkAll"  @click="allExchange">全部</div>
        </div>
        <div class="selectBox selectBox3 desc">
          <div class="li">
            &nbsp;&nbsp;&nbsp;&nbsp;手续费:
            <span>10.00</span> ETS
          </div>
          <div class="li">
            最低限度:
            <span>100.00</span> ETS
          </div>
          <div class="li">
            最大限度:
            <span>10000.00</span> ETS
          </div>
        </div>
        <div class="nextBbtn flexcenter submitBtn" @click="toExchangeCoin">下一步</div>
      </div>
    </div>
    <div v-if="maskwindow" @click="closeWindow" class="maskwindow" @touchmove.prevent></div>
    <div v-if="selectCoin" class="selectCoin">
      <div class="defineCancel flex flexjcsb">
        <div class="cancel flexcenter" @click="closeSelectcoin">取消</div>
        <div class="define flexcenter" @click="sureSelectcoin">确定</div>
      </div>
      <mt-picker :slots="slots" @change="onValuesChange"></mt-picker>
    </div>
    <div v-if="sureExchange" class="sureDuihuan popupWindow ovh">
      <div class="title">确认兑换信息</div>
      <div class="con">
        <div class="line flex flexjcsb">
          <div class="text">兑换</div>
          <div class="num">{{this.exchangeNumber}} ETS</div>
        </div>
        <div class="line flex flexjcsb">
          <div class="text">接收</div>
          <div class="num">{{this.receiveNumber}} {{this.toExchange}}</div>
        </div>
      </div>
      <div class="btnul flex flexjcsb">
        <div class="cancel flexcenter" @click="closeExchangeCoin">取消</div>
        <div class="define flexcenter submitBtn" @click="exchangeCoin">下一步</div>
      </div>
    </div>
  </div>
</template>
<script>
import { Toast } from 'mint-ui';
import { MessageBox } from "mint-ui";
import { ethers } from "ethers";

export default {
  async created() {
    this.$store.state.player.beInvitedCode =  this.$route.query.code; 
  },
  data() {
    return {
      slots: [
        {
          flex: 1,
          values: [
            "USDT",
            "ETH"
          ],
          className: "slot1",
          textAlign: "right"
        }
      ],
      maskwindow: false,
      selectCoin: false,
      sureExchange: false,
      exchangeNumber: "",
      receiveNumber: "",
      selectChangeCoin: "",
      fromExchange: "ETS",
      toExchange: "ETH",
      selectType: ""
    };
  },
  methods: {
    closeWindow() {
      this.maskwindow = false;
      this.sureExchange = false;
      this.selectCoin = false;
    },
    closeExchangeCoin() {
      this.exchangeNumber = "";
      this.maskwindow = false;
      this.sureExchange = false;
      this.selectCoin = false;
    },
    exchangeCoin() {
      let coin  =  0;
      if(this.toExchange=='ETH'){
        coin = 10;
      }
      else{
        coin = 20;
      }
      console.log("exchangeNumber  " + ethers.utils.parseUnits(this.exchangeNumber,"Mwei"));
      this.$store.state
      .etherNetContractInstance()
      .exchange(ethers.utils.parseUnits(this.exchangeNumber,"Mwei"),coin)
      .then(tx => {
        console.log(" exchange  " + tx);
        Toast("操作成功! 交易ID:" + tx.hash); 
        this.closeExchangeCoin();
      });
    },
    toExchangeCoin() {
      console.log("toExchangeCoin  " +  this.exchangeNumber);
      console.log("toExchangeCoin  " +  parseFloat(this.exchangeNumber));
      if(this.glabal.isJoinGame(this.$store.state.player)==false){
        this.isJoinGame();
        return;
      }

      if(this.glabal.isNull(this.exchangeNumber)||parseFloat(this.exchangeNumber)<=0)
      {
        Toast('请输入兑换数量');
        return;
      }
      if(this.glabal.isNull(this.$store.state.priceInfo.ethPrice)||parseFloat(this.$store.state.priceInfo.ethPrice)<=0||
      this.glabal.isNull(this.$store.state.priceInfo.etsPrice)||parseFloat(this.$store.state.priceInfo.etsPrice)<=0||
      this.glabal.isNull(this.$store.state.priceInfo.usdtPrice)||parseFloat(this.$store.state.priceInfo.usdtPrice)<=0){
        Toast('兑换价格信息异常');
        return;
      }

      if(this.toExchange=='ETH'){
        // console.log("toExchange " + parseFloat(this.$store.state.priceInfo.etsPrice) );
        // console.log("toExchange " + parseFloat(this.exchangeNumber) );
        // console.log("toExchange " +  parseFloat(this.$store.state.priceInfo.ethPrice));
        // console.log("toExchange " +  parseFloat(this.$store.state.priceInfo.usdtPrice));
        this.receiveNumber = parseFloat(this.$store.state.priceInfo.etsPrice)*parseFloat(this.exchangeNumber) / parseFloat(this.$store.state.priceInfo.ethPrice);
        this.receiveNumber = this.glabal.toDecimal(this.receiveNumber);
      }
      else{
        this.receiveNumber = parseFloat(this.$store.state.priceInfo.etsPrice)*parseFloat(this.exchangeNumber) /  parseFloat(this.$store.state.priceInfo.usdtPrice);
        this.receiveNumber = this.glabal.toDecimal(this.receiveNumber);
      }
     
      this.maskwindow = true;
      this.sureExchange = true;
    },
    closeSelectcoin() {
      event.stopPropagation();
      this.selectCoin = false;
      this.maskwindow = false;
    },
    sureSelectcoin() {
      if (this.selectType == "fromCoin") {
        this.fromExchange = this.selectChangeCoin;
      } else if (this.selectType == "toCoin") {
        this.toExchange = this.selectChangeCoin;
      }
      this.maskwindow = false;
      this.selectCoin = false;
    },
    showSelectCoin(type) {
      this.maskwindow = true;
      this.selectCoin = true;
      this.selectType = type;
    },
    onValuesChange(picker, values) {
      console.log(picker);
      console.log(values);
      this.selectChangeCoin = values[0];
    },
    allExchange() {
      this.exchangeNumber  = this.$store.state.player.etsFreeAmount;
      console.log("allExchange " + this.exchangeNumber );
    },
    isJoinGame() {
      let _this = this;
      MessageBox({
        $type: 'prompt',
        title: "加入游戏",
        message: "你还未加入游戏<br>合约交易完成后再进行相应操作",
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        closeOnClickModal: false,   
        showCancelButton: true,    
        inputErrorMessage: "请输入正确的提现数量",
        showInput: false
      }).then(action => {
        if (action === 'confirm') {
          if (this.glabal.isNull(_this.$store.state.player.beInvitedCode)) {
            Toast('没有识别到正确的邀请码');
            return;
          }
          let inviteCode = random(6);
          _this.$store.state
          .etherNetContractInstance()
          .joinGame(inviteCode,_this.$store.state.player.beInvitedCode)
          .then(info => {
            console.log(" buyLevel  " + info);
          });
        }
      }).catch(err => {
        console.log(err)
      })
    },
  },
  components: {}
};
</script>
<style rel="stylesheet/scss" lang="scss" scoped>
.nextBbtn {
  width: 93%;
  height: 40px;
  margin: 0 auto;
}
.selectBox {
  border-bottom: 1px solid #e9eaee;
}
.coinType .selectBox:last-child,
.selectBox3 {
  border-bottom: none;
}
.trade {
  width: 100%;
  height: 100%;
  background: url("../../../static/tradeBg.png");
  background-size: 100% 100%;
  text-align: left;
  .commonHead {
    color: #fff;
    .caidan {
      position: absolute;
      right: 0;
    }
  }
  .main {
    border-radius: 6px;
    background-color: #f5f5f5;
    .title {
      padding: 30px 0 0 0;
      color: var(--basetext);
      font-size: 16px;
      font-weight: 600;
      .underline{
        position:absolute;
        bottom: 0;
        width: 100%;
        height: 5px;
        background-color: #ff682f;
         opacity: 0.4;
        left: 0;
        z-index: 11;
      }
    }
    .coinType {
    }

    .coinMoney {
      margin-top: 15px;
      padding-bottom: 30px;
      input {
        border: none;
        outline: none;
      }

      .type {
        width: 50px;
        color: #bf7960;
        font-weight: 600;
      }
      .checkAll {
        color: var(--basetext);
        font-weight: 600;
      }
      .desc {
        .li {
          color: var(--morentext);
          font-size: 14px;
          padding: 3px 0;
        }
      }
    }
  }
}
</style>



